@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  html {
    font-family: 'Inter', system-ui, sans-serif;
  }
  
  body {
    @apply text-gray-100;
    min-height: 100vh;
    background: linear-gradient(to bottom right, #1a1a1a, #0d0d0d, #1a1a1a);
  }
}

/* Tiled background with Gun Guys II logo - image set via JavaScript */
body {
  background-size: 200px 200px;
  background-repeat: repeat;
  background-attachment: fixed;
  background-position: 0 0;
}

body::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to bottom right, rgba(26, 26, 26, 0.65), rgba(13, 13, 13, 0.65), rgba(26, 26, 26, 0.65));
  z-index: 0;
  pointer-events: none;
}

/* Ensure content is above background */
body > * {
  position: relative;
  z-index: 1;
}

@layer components {
  .btn-primary {
    @apply bg-gray-700 hover:bg-gray-600 text-white font-medium py-2 px-4 rounded-lg transition-colors duration-200;
  }
  
  .btn-secondary {
    @apply bg-gray-800 hover:bg-gray-700 text-gray-100 font-medium py-2 px-4 rounded-lg transition-colors duration-200;
  }
  
  .btn-danger {
    @apply bg-gray-700 hover:bg-gray-600 text-white font-medium py-2 px-4 rounded-lg transition-colors duration-200;
  }
  
  .input-field {
    @apply w-full px-3 py-2 bg-gray-800 border border-red-800 text-gray-100 rounded-lg focus:outline-none focus:ring-2 focus:ring-red-700 focus:border-red-700;
  }
  
  .card {
    @apply bg-white rounded-lg shadow-lg border-2 border-red-800 p-6;
  }
  
  .badge {
    @apply inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium;
  }
  
  .badge-success {
    @apply bg-green-600/40 text-green-100 border border-green-500/60;
  }
  
  .badge-warning {
    @apply bg-yellow-500/40 text-yellow-900 border border-yellow-400/60;
  }
  
  .badge-danger {
    @apply bg-red-600/40 text-red-100 border border-red-500/60;
  }
  
  .badge-info {
    @apply bg-blue-600/40 text-blue-100 border border-blue-500/60;
  }
}

/* Form input styling for better readability - applies to white card inputs only */
input[type="text"]:not(.input-field),
input[type="email"]:not(.input-field),
input[type="password"]:not(.input-field),
input[type="tel"]:not(.input-field),
input[type="number"]:not(.input-field),
input[type="date"]:not(.input-field),
input[type="time"]:not(.input-field),
input[type="url"]:not(.input-field),
textarea:not(.input-field),
select:not(.input-field) {
  color: #1f2937 !important; /* text-gray-800 - dark text */
}

input:not(.input-field)::placeholder,
textarea:not(.input-field)::placeholder {
  color: #6b7280 !important; /* text-gray-500 - medium gray for placeholders */
  opacity: 1;
}

input:not(.input-field):focus,
textarea:not(.input-field):focus {
  color: #111827 !important; /* text-gray-900 - even darker when focused */
}

/* Ensure select dropdowns have readable text (white card selects only) */
select:not(.input-field) {
  color: #1f2937 !important;
}

select:not(.input-field) option {
  color: #1f2937 !important;
  background-color: white !important;
}

/* Ensure .input-field maintains light text on dark background */
.input-field {
  color: #f3f4f6 !important; /* text-gray-100 - light text for dark inputs */
}

.input-field::placeholder {
  color: #9ca3af !important; /* text-gray-400 - lighter placeholder for dark inputs */
  opacity: 1;
}

.input-field:focus {
  color: #ffffff !important; /* white text when focused on dark inputs */
}

/* Custom scrollbar */
::-webkit-scrollbar {
  width: 6px;
}

::-webkit-scrollbar-track {
  background: #1a1a1a;
}

::-webkit-scrollbar-thumb {
  background: #404040;
  border-radius: 3px;
}

::-webkit-scrollbar-thumb:hover {
  background: #505050;
}

/* Loading spinner */
.spinner {
  @apply animate-spin rounded-full h-4 w-4 border-b-2 border-gray-500;
}

/* Target ring animation */
@keyframes target-ring {
  0% {
    transform: scale(0.8);
    opacity: 1;
  }
  100% {
    transform: scale(1.2);
    opacity: 0;
  }
}

.target-ring {
  animation: target-ring 2s infinite;
}
